@keyframes octocat-wave{0%,100%{transform:rotate(0)}20%,60%{transform:rotate(-25deg)}40%,80%{transform:rotate(10deg)}}.github-corner{position:absolute;top:0;border:0;right:0;pointer-events:none}.github-corner svg{color:#fff;fill:#000;display:block}.github-corner svg g{pointer-events:initial}.github-corner svg g:hover .octo-arm{animation:octocat-wave 560ms ease-in-out}.github-corner svg g .octo-arm{transform-origin:130px 106px}

.character-planner{width:100%;height:100%;display:flex}.character-planner>:first-child{flex:0 0 auto}.character-planner>:nth-child(2){flex:1 0}

